<template>
    <CommonPage
        :editRules="editRules"
        :addRules="addRules"
        :otherVariable="otherVariable"
        :ajaxConfig="ajaxConfig"
        :propData="propData">
    </CommonPage>
</template>
<script>
    const pageName = '';
    import CommonPage from '$src/components/CommonPage'
    export default {
        components: {
            CommonPage
        },
        data() {
            return {
                otherVariable: {
                    detailTitle: `${pageName}`,
                    addTitle: `${pageName}`,
                    isMulSelect: false
                },
                editRules: {

                    'fname':[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    'pvcTypeId':[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    'meterWght':[
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },
                addRules: {
                    'fcode':[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    'fname':[
                        { required: true, message: '', trigger: 'blur' }
                    ],

                    'pvcTypeId':[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    'meterWght':[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    'inactive':[
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },
                ajaxConfig: {
                    //TODO
                    dataObj: {
                        url: '',
                        method: 'fetch'
                    },
                    addObj: {
                        url: '',
                        method: 'fetch'
                    },
                    updateObj: {
                        url: '',
                        method: 'fetch'
                    },
                    deleteObj: {
                        url: '',
                        method: 'fetch'
                    }
                },
                propData: [
                    {
                        'name':'fid',
                        'prop':'fid',
                        'type':'normal',
                        'isMainSearch':false,
                        'isMain':true,
                        'placeholder':'',

                    },
                    {
                        'name':'',
                        'prop':'fcode',
                        'type':'normal',
                        'isMainSearch':false,
                        'isMain':false,

                    },
                    {
                        'name':'',
                        'prop':'fname',
                        'type':'normal',
                        'isMainSearch':false,
                        'isMain':false,

                    },
                    {
                        'name':'',
                        'prop':'pvcTypeId',
                        'isRead':true,
                        'type':'normal',
                        'isMainSearch':true,
                        'isMain':false,
                        'placeholder':'',
                    },
                    {
                        'name':'',
                        'prop':'meterWght',
                        'type': 'select',
                        'isMainSearch': false,
                        'isMain': false,
                        'disabledInput': true,
                        'showRemark':true,
                        'selectList': []
                    },
                    {
                        'name':'',
                        'prop':'meterWght',
                        'type': 'select',
                        'isMainSearch': false,
                        'isMain': false,
                        'disabledInput': true,
                        'showRemark':true,
                        'selectList': []
                    },
                    {
                        'name':'',
                        'prop':'',
                        'type':'normal',
                        'isMainSearch':false,
                        'isMain':false,
                        'disabledInput': true,
                        'showRemark':true,
                    },
                    {
                        'name':'',
                        'prop':'',
                        'type':'normal',
                        'isMainSearch':false,
                        'isMain':false,
                        'disabledInput': true,
                        'showRemark':true,
                    },
                    {
                        'name':'',
                        'prop':'',
                        'type':'date',
                        'isMainSearch':false,
                        'isMain':false,
                        'disabledInput': true,
                        'showRemark':true,
                    },
                    {
                        'name':'',
                        'prop':'',
                        'type':'date',
                        'isMainSearch':false,
                        'isMain':false,
                        'disabledInput': true,
                        'showRemark':true,
                    },
                    {
                        'name':'',
                        'prop':'',
                        'type':'normal',
                        'isMainSearch':false,
                        'isMain':false,
                        'disabledInput': true,
                        'showRemark':true,
                    },

                ]
            }
        },
        mounted() {
            this.getStripType()
        },
        methods: {
            getStripType() {
                this.fetch('/basedata/Mat/PvcType/selectByQueryModel?pageSize=100',{})
                    .then(result => {
                        if(result.status === 200 && result.data && result.data.list) {
                            this.propData[3].selectList = this.formatArray(result.data.list,'fname','fid');
                        }else {
                            this.propData[3].selectList = [];
                        }
                    })
            }
        }
    }
</script>



